
"use client";

import Link from 'next/link';
import Image from 'next/image';
import { Button } from '@/components/ui/button';
import { Card, CardContent } from '@/components/ui/card';
import { products } from '@/lib/data';
import { useToast } from '@/hooks/use-toast';
import { useRouter } from 'next/navigation';

export default function CustomerProductsPage() {
    const { toast } = useToast();
    const router = useRouter();

    const handleRentNow = (productName: string) => {
        toast({
            title: "Item Added to Cart",
            description: `${productName} has been added to your rental cart.`,
        });
        // In a real app, this would likely navigate to a checkout page
        // or update a cart state. For this prototype, we'll navigate to orders.
        router.push('/dashboard/customer/orders');
    }

  return (
    <div className="container mx-auto py-8">
        <div className="text-center mb-12">
            <h1 className="text-4xl font-bold tracking-tight">Browse Our Products</h1>
            <p className="mt-4 text-lg text-muted-foreground">Find the perfect gear for your next adventure.</p>
        </div>
        <div className="grid gap-8 md:grid-cols-2 lg:grid-cols-3 xl:grid-cols-4">
            {products.map((product) => (
            <Card key={product.id} className="overflow-hidden transition-all duration-300 ease-in-out hover:shadow-lg hover:-translate-y-1">
                <CardContent className="p-0">
                    <Image
                        src={product.imageUrl}
                        alt={product.name}
                        width={600}
                        height={400}
                        className="object-cover w-full h-56"
                        data-ai-hint={`${product.category} product`}
                    />
                    <div className="p-6">
                        <h3 className="text-xl font-semibold">{product.name}</h3>
                        <p className="mt-2 text-muted-foreground">{product.category}</p>
                        <div className="flex items-baseline justify-between mt-4">
                            <p className="text-lg font-bold text-primary">
                                ${product.dailyRate.toFixed(2)}
                                <span className="text-sm font-normal text-muted-foreground">/day</span>
                            </p>
                            <Button onClick={() => handleRentNow(product.name)}>Rent Now</Button>
                        </div>
                    </div>
                </CardContent>
            </Card>
            ))}
        </div>
    </div>
  );
}
